@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;

}

html {
    height: 100%;
}
body {
    height: 100%;
    margin: 0;
    background:linear-gradient( 165deg, rgba(0,128,128,1)0%, rgba(7,27,82,1)100% );
    background-repeat: no-repeat;
    background-attachment: fixed;
    
}


#root{
   
        padding-top: 2rem;
}
h1{
    font-family: 'Press Start 2P', cursive;
    /* color: rgb(160, 225, 255); */
    color: #17eee7;
    text-shadow:  1px 1px 1px rgba(7,27,82, 1);
}
hr{
    border: 1px solid rgba(160, 225, 255, .25);
    display: inline-block;
    width: 90%;
    margin: 0;
}
input{
    background-color: rgb(160, 225, 255);
}
::-webkit-input-placeholder{
    color:  rgba(7,27,82,1);
    text-transform: capitalize;
}
:-ms-input-placeholder{
    color:  rgba(7,27,82,1);
    text-transform: capitalize;
}
::-ms-input-placeholder{
    color:  rgba(7,27,82,1);
    text-transform: capitalize;
}
::placeholder{
    color:  rgba(7,27,82,1);
    text-transform: capitalize;
}
